import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the BotAsistantProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class BotAsistantProvider {

  constructor(public _http: HttpClient) {
    console.log('Hello BotAsistantProvider Provider');
  }



  hablarBot(texto){
    let url = "https://gateway.watsonplatform.net/assistant/api/v1/workspaces/8ae11929-40de-4a36-ae4f-33e3812a19a1/message?version=2018-09-20";
    let access = "Basic ZGEwYmNmZjktOGExZS00MWUyLWE0YTEtNjQxOTk0MDFkMjQxOm9BcUZZemlDWXgwTQ==";
    let headers = {"Authorization":access,"Content-Type":"application/json"};

    let body = {"input": {"text": texto}};
	console.log(JSON.stringify(body));

    return this._http.post(url,body,{headers});

  }

}



